<template>
  <div id="app">
  <div class="page-container">
    <md-app md-mode="reveal">
      <md-app-toolbar class="md-primary">
        <md-button class="md-icon-button" @click="menuVisible = !menuVisible">
          <md-icon>menu</md-icon>
        </md-button>
        <span class="md-title">网站访问分析</span>
      </md-app-toolbar>

      <md-app-drawer :md-active.sync="menuVisible" md-persistent="full" >
        <md-toolbar class="md-transparent" md-elevation="0">
         <md-avatar alt="Avatar">
        <img src="../assets/education.png">
        </md-avatar></md-toolbar>
           <div class="md-toolbar-section-end">
            <md-button class="md-icon-button md-dense" @click="toggleMenu">
              <md-icon>keyboard_arrow_left</md-icon>
            </md-button>
          </div>
        <md-list>
          <md-list-item>
            <md-icon>dashboard</md-icon>
            <span class="md-list-item-text"><router-link to="/ui/dashboard">DASHBOARD</router-link></span>
          </md-list-item>

          <md-list-item>
            <md-icon>account_box</md-icon>
            <span class="md-list-item-text">PROFILE</span>
          </md-list-item>
          <md-list-item>
            <md-icon>web</md-icon>
            <span class="md-list-item-text">MYSITES</span>
          </md-list-item>

          <md-list-item>
            <md-icon>settings_applications</md-icon>
            <span class="md-list-item-text">SETTINGS</span>
          </md-list-item>
        </md-list>
      </md-app-drawer>

      <md-app-content>
            <router-view/>
      </md-app-content>
    </md-app>
  </div>

  </div>
</template>

<script>
/* eslint-disable */
export default {
  name: 'MainContent',
  data: () => ({
    menuVisible: false
  }),
  methods: {
      toggleMenu () {
        this.menuVisible = !this.menuVisible
      }
    }
}
</script>

<style scoped >
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 0px;
  padding: 0px;
}
.md-app {
  border: 1px solid rgba(#000, 0.12);
}
.md-drawer {
  width: 15em;
  max-width: calc(100vw - 125px);
}
</style>



