<template>
  <div class="login-box">
    <div class="auth-box">
      <md-avatar alt="Avatar">
        <img src="../assets/logo.jpg">
      </md-avatar>
      <div>
       <h4> Welcome to Count analysis,it takes you insight of your business</h4>
      </div>
    <md-field>
      <label>username</label>
      <md-input v-model="username"></md-input>
      <!-- <span class="md-helper-text">your username</span> -->
    </md-field>

    <md-field>
      <label>password</label>
      <md-input v-model="password" type="password"></md-input>
      <!-- <span class="md-helper-text">your password</span> -->
    </md-field>
    </div>
    <div>
       <md-button class="md-raised" disabled>register</md-button>
       <md-button class="md-raised" v-on:click="onLogin()">login</md-button>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import Vue from 'vue'
export default {
  name: 'Login',
  methods:{
    onLogin:function(){
      let credential={
        userName:this.username,
        password:this.password
      }
      Vue.axios.post(this.ServiceBaseUrl+"/login",credential).then((response) => {
        let auth={
          isLoggedIn: true,
          userid: response.data.userid,
          token: response.data.token
        }
        console.log(auth)
        this.$store.commit('setLoginState',auth)
        this.$router.push({path:'/ui/dashboard'})
      }).catch(function(error){
        console.log(error)
      })
    }
  },
  data () {
    return {
      username: null,
      password: null
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.login-box {
  text-align: center; 
}
.auth-box{
    margin:auto; 
    width: 300px; 
    padding-top:10%; 
}
</style>


