<template>
  <div class="chartstyle">
    <div v-for="item in sitesOptions" :key="item.title.text" >
      <chart  :options="item"></chart>
    </div>
  </div>
</template>
<script>
/* eslint-disable */
import Vue from "vue";

export default {
  name: "Dashboard",
  created: function() {
    this.statsBySite();
    this.stateSiteByDay()
  },
  methods: {
    statsBySite: function() {
      let self = this;
      if (this.$store.getters.auth.isLoggedIn == true) {
        let userid = this.$store.getters.auth.userid;
        let token = this.$store.getters.auth.token;
        Vue.axios
          .get(
            this.ServiceBaseUrl +
              "/stats/user/" +
              userid +
              "/bysite?token=" +
              token
          )
          .then(function(res) {
            let option = self.getOption()
            option.title.text="所有网站统计访问统计"
            res.data.forEach(site => {
              option.xAxis.data.unshift(site.siteName);
              option.series[0].data.unshift(site.hits);
            });
            self.sitesOptions.unshift(option);
          })
          .catch(function(error) {
            alert(error);
          });
      } else {
        this.$router.push({ path: "/login" });
      }
    },
    getOption:function(){
      return {
             
              title:{
                text:"行为分析"
              },
              xAxis: {
                type: "category",
                data: [],
                 axisLabel:{
                interval:0,
                rotate:"40"
              }
              },
              yAxis: {
                type: "value"
              },
              series: [
                {
                  data: [],
                  type: "bar"
                }
              ],
              tooltip:{
                show: true,
                trigger: 'item'
              },
              itemStyle: {
                normal: {
                  show: true,
                  position: 'top',
                  textStyle: {
                    color: 'black'
                  }
                }
              }
            }
    },
    stateSiteByDay:function() {
      let self=this
      let auth=this.$store.getters.auth
      let userid = auth.userid
      let token = auth.token
      console.log(auth)
      Vue.axios.get(self.ServiceBaseUrl +"/user/" +userid +"/site?token=" +token).then(function(res){
        res.data.forEach(function(site){
         
          Vue.axios.get(self.ServiceBaseUrl+"/stats/user/"+userid+"/site/"+site.id+"/byday?token="+token).then(function(res){
            let option=self.getOption()
            let hitsInfo=res.data; 
            console.log(res.data)
             hitsInfo.forEach(function(hit){
               option.xAxis.data.unshift(hit.day)
               option.series[0].data.unshift(hit.hits)
             })
             option.title.text="网站 "+site.name+"按天访问统计"
             self.sitesOptions.push(option)
            }).catch(function (error){
              console.log(error)
            }),
            Vue.axios.get(self.ServiceBaseUrl+"/stats/user/"+userid+"/site/"+site.id+"/bypathname?token="+token).then(function(res){
            let hitsInfo=res.data; 
            let option=self.getOption()
            console.log(res.data)
             hitsInfo.forEach(function(hit){
               option.xAxis.data.unshift(hit.pathName)
               option.series[0].data.unshift(hit.hits)
             })
             option.title.text="网站 "+site.name+"按路径访问"
            // option.series[0].type='pie'
             self.sitesOptions.push(option)
            }).catch(function (error){
              console.log(error)
            })
          })
        }).then(function(error){
          console.log(error)
        })
      }
    },
  data: function() {
    return {
      sitesOptions: []
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.echarts {
  height: 300px;
  width: 350px;
  margin: auto;
}
.chartstyle {
  padding-top: 3em;
  margin: auto;
}
</style>



